.star {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 15px 18px 15px;
}

.star_sphere {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

.star_avatar {
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.star_nickname {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 15px;
  color: black;
  margin-top: 9px;
}

.star_role {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #726F6F;
  margin-top: 6px;
}